.square {
  flex: 1 0 32%;
  height: 100px;
  font-size: 30px;
  text-align: center;
  vertical-align: middle;
  line-height: 100px;
  border: 1px solid black;
  box-sizing: border-box;
}
.board {
  display: flex;
  flex-wrap: wrap;
  max-width: 300px;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.game-info {
  margin-top: 10px;
  text-align: center;
}
